body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

#header {
    height: 79px;
    background-color: #141e2a;
}

#logo {
    padding: 15px;
    padding-left: 100px;
    position: absolute;
   
}

#logopic {
    width: 162px;
    height: 47px;
    
}

.itainer {
    display: flex;
    justify-content: flex-end;
    padding-right: 100px;
    position: relative;
    
}

#message {
    height: 25px;
    width: 25px;
    padding-top: 20px;
}

#notepic {
    height: 25px;
    width: 25px;
    padding-top: 20px;
}

#searchpic {
    height: 25px;
    width: 28px;
    border-radius: 50%;
    margin-top: 3px;
    padding-top: 20px;
    
}


#headerdiv {
    height: 24px;
    width: 1.5px;
    background-color: #384453;
    margin-top:20px;
}

#userpic {
    height: 45px;
    width: 55px;
    border-radius: 50%;    
    padding-top: 20px;
}

#userhold {
    margin-top: -9px;
}


#navbox a{
    height: 60px;
    display: inline-block;
    text-align: center;
    border-bottom: 4px solid transparent;
    text-decoration: none;
}

a {
    color: #8492a6;
   margin-right: 32px;
   padding-top: 20px;
   font-family: 'Open Sans';
   font-size: 12px;
}

#navbox a:hover {
    
    border-bottom: 4px solid #c0ccda;
}

button {
    background-color: #ffffff;
    border-radius: 20px;
    width: 103px; 
    padding-top: 5px;
    padding-bottom: 5px;
    font-family: 'Open Sans';
    font-size: 9px;
    color: #29d879;
    font-weight: bold;
    border-style: solid;
    border-width: 1px;
    border-right: #29d879;
    border-left: #00a983
}


#eventbox {
    height: 598px;
    text-align: center;
}

#eventtext {
    font-family: roboto;
    font-weight: bold;
    font-size: 28px;
    padding-top: 100px;
    color: #1f2d3d;
}

.boxes {
    height: 251px;
    width: 260px;
    border-radius: 25px;
    background-color: #ffffff;
    border: 1px solid #eff2f7;
    margin: 20px ;
    display: inline-block;
}

.evento {
    color: #1f2d3d;
    font-size: 35px;
    font-family: 'roboto';
}

.eventop {
    color: #8492a6;
    font-size: 14px;
    font-family: 'open sans';
    text-align: center;
    padding-left: 30px;
}

.boxe:hover{
    border: 2px solid #8492a6;
    box-shadow: 2px grey;
}

.tourpic {
    padding-top: 35px;
    height: 65px;
    width: 65px;
}

.calpic {
    height: 65px;
    width: 70px;
    padding-top: 35px;
}

#recur {
    background: -webkit-linear-gradient(#1fd2ff 0%, #42ffd8 100%);
    background: linear-gradient(#1fd2ff 0%, #42ffd8 100%)
}

.eventip {
    color: #ffffff;
    font-size: 35px;
    font-family: 'roboto';
}

.eventi {
    color: #ffffff;
    font-size: 14px;
    font-family: 'open sans';
    text-align: center;
    padding-left: 30px;
}

.refresh {
    height: 65px;
    width: 70px;
    padding-top: 35px;
}
#footerdiv {
    height: 73px;
    border-top: 1px solid #e0e6ed;
    border-bottom: 1px solid black;
    display: flex;
    justify-content: flex-start;
    padding-left: 100px;
    padding-top: 10px;
    flex-direction: row;
    
}

footer {
    display: flex;    
}

#wordsdiv{
    display: flex;
    position: absolute;
}

a {
    text-decoration: none;
    font-size: 12px;
    font-weight: bold;
    
}

a:hover {
    text-decoration: underline;
}

p {
    padding-right: 32px;
    font-size: 12px;
    color: #8492a6;
    padding-top: 7px;
}

.and {
    margin-right: -28px;
    margin-left: -28px;
}

#icons {
    padding-left: 700px;
    padding-right: 100px;
    padding-top: 10px;
       
}

.sociali {
    width: 24px;
    margin: -14px;
}

.twosquare {
    padding-bottom: 2px;
}





