#eventbox {
    height: 598px;
    text-align: center;
}

#eventtext {
    font-family: roboto;
    font-weight: bold;
    font-size: 28px;
    padding-top: 100px;
    color: #1f2d3d;
}

.boxes {
    height: 251px;
    width: 260px;
    border-radius: 25px;
    background-color: #ffffff;
    border: 1px solid #eff2f7;
    margin: 20px ;
    display: inline-block;
}

.evento {
    color: #1f2d3d;
    font-size: 35px;
    font-family: 'roboto';
}

.eventop {
    color: #8492a6;
    font-size: 14px;
    font-family: 'open sans';
    text-align: center;
    padding-left: 30px;
}

.boxe:hover{
    border: 2px solid #8492a6;
    box-shadow: 2px grey;
}

.tourpic {
    padding-top: 35px;
    height: 65px;
    width: 65px;
}

.calpic {
    height: 65px;
    width: 70px;
    padding-top: 35px;
}

#recur {
    background: linear-gradient(#1fd2ff 0%, #42ffd8 100%)
}

.eventip {
    color: #ffffff;
    font-size: 35px;
    font-family: 'roboto';
}

.eventi {
    color: #ffffff;
    font-size: 14px;
    font-family: 'open sans';
    text-align: center;
    padding-left: 30px;
}

.refresh {
    height: 65px;
    width: 70px;
    padding-top: 35px;
}