
#navbox a{
    height: 60px;
    display: inline-block;
    text-align: center;
    border-bottom: 4px solid transparent;
    text-decoration: none;
}

a {
    color: #8492a6;
   margin-right: 32px;
   padding-top: 20px;
   font-family: 'Open Sans';
   font-size: 12px;
}

#navbox a:hover {
    
    border-bottom: 4px solid #c0ccda;
}

button {
    background-color: #ffffff;
    border-radius: 20px;
    width: 103px; 
    padding-top: 5px;
    padding-bottom: 5px;
    font-family: 'Open Sans';
    font-size: 9px;
    color: #29d879;
    font-weight: bold;
    border-style: solid;
    border-width: 1px;
    border-right: #29d879;
    border-left: #00a983
}

